.header {
  background-color: #0c0032;
  grid-area: header;
  box-shadow: 0 0 2px rgba(250, 250, 250, 0.3);
  border: 1px solid rgba(250, 250, 250, 0.3);
  border-radius: 20px;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

.header img {
  height: 100px;
}

.text {
  color: white;
}

.login {
  color: white;
  text-decoration: none;
  margin-left: 20px;
}